.btnBack{
    width: 50%;
}

.btnBack button{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background-color: transparent;
    border-radius: 10px;
    font-size: 15px;
    cursor: pointer;
    transition: .7s;
}
.btnBack button:hover{
    opacity: 0.2;
    background-color: white;
    color: #000;
}
.btnBack button span{
    margin-right: 10px;
}
.btnBack a{
    text-decoration: none;
}