.card-vagas{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    border-radius: 10px;
    width: 50%;
    height: 170px;
    margin: 10px;
    padding: 10px;
    transition: .7s;
    cursor: pointer;
}
.card-vagas:last-child{
    margin-bottom: 50px;
}
.card-vagas:hover{
    opacity: 0.4;
    transform: translateX(5%);
}

.card-vagas img{
    border-radius: 50%;
    width: 70px;
}

.card-vagas-info{
    padding: 20px;
}

.card-vagas-info h3{
    margin-bottom: 10px;
    padding: 0 10px;
    color: #000;
}

.card-vagas-info p{
    color: #808080;
    padding: 0 10px;
}
.login{
    font-size: 20px;
}

.card-vagas-info ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    width: 100%;
    padding: 5px;
}
.card-vagas-info li{
    font-weight: bold;
    list-style: none;
    padding: 5px 10px;
    color: #fff;
}
.card-vagas a{
    display: flex;
    align-items: center;
    text-decoration: none;
    width: 100%;
}

.date{
    margin-top: 10px;
}

@media (max-width: 1630px){
    .card-vagas{
        width: 90%;
        height: 200px;
    }
}

@media (max-width: 780px){
    .card-vagas-info h3{
        font-size: 15px;
    }
    .card-vagas-info p{
        font-size: 15px;
    }
    .card-vagas-info li{
        font-size: 10px;
    }
    .card-vagas-info .date{
        font-size: 12px;
    }
}

@media (max-width: 415px){
    .card-vagas-info ul{
        display: none;
    }
}