.card{
    display: flex;
    align-items: center;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
    width: 100%;
    cursor: pointer;
    transition: .7s;
    margin-top: 30px;
}
.card:hover{
    opacity: 0.4;
    transform: translateX(5%);
}

.card img{
    width: 100px;
}

.card-info{
    padding: 20px;
}

.card h3{
    color: #000;
    margin-bottom: 10px;
    font-family: sans-serif;
    font-size: 20px;
}

.card p{
    color: #808080;
    font-size: 20px;
    font-family: sans-serif;
}

.link{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    height: 150px;
    text-decoration: none;
}


@media (max-width: 1310px){
    .link{
        width: 90%;
    }
    .App{
        height: 115vh;
    }
}

@media (max-width: 967px){
    .card img{
        width: 80px;
    }
    .card h3{
        font-size: 17px;
    }
    .card p{
        font-size: 15px;
    }
}

@media (max-width: 500px){
    .App{
        height: 120vh;
    }
}

@media (max-width: 375px){
    .App{
        height: 160vh;
    }
}