footer{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100vw;
    position: fixed;
    bottom: 0;
    height: 40px;
    color: #fff;
    background-color: #c4c4c4;
}

footer p{
    color: red;
    padding: 5px;
    font-size: 20px;
}

footer span{
    display: flex;
    align-items: center;
    font-size: 15px;
    font-weight: 500;
    color: #000;
}

.social-medias{
    display: flex;
}

.social-medias a{
    display: flex;
    cursor: pointer;
    transition: .7s;
}

.social-medias a:hover{
    opacity: 0.5;
}
