.vagas{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.vagas-header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    width: 50%;
    padding: 20px 0;
}

.vagas-header h2{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    padding: 20px;
}

.vagas-header img{
    width: 150px;
}

@media (max-width: 415px){
    .vagas img{
        width: 100px;
    }
    .vagas h2{
        display: none;
    }
}